const routeEndpoint = "https://dev.unit.la/guiapatrimonio/wp-json/wp/v2/ruta";
const circuitEndpoint =
  "https://dev.unit.la/guiapatrimonio/wp-json/wp/v2/circuitos";

async function fetchJson(url: string): Promise<[Response | undefined, any]> {
  const response = await fetch(url);

  if (response.ok) {
    return [response, await response.json()];
  } else {
    return [undefined, undefined];
  }
}

// s=>{for(var i=0,h=9;i<s.length;)h=Math.imul(h^s.charCodeAt(i++),9**9);return h^h>>>9}
// https://stackoverflow.com/a/52171480
const hash32 = (input: string) => {
  for (var i = 0, h = 9; i < input.length; )
    h = Math.imul(h ^ input.charCodeAt(i++), 9 ** 9);

  return h ^ (h >>> 9);
};

const sleep = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms));

function sanitize(input: string): string {
  return input.replace("&#8216;", "'");
}

export { fetchJson, hash32, sleep, routeEndpoint, circuitEndpoint, sanitize };
